header{
    position:fixed;
    width: 100%;
    height: 8vh;
    background-color: black;
    display: flex;
    justify-content: space-around;
    align-items: center;
    z-index: 99;
}

.Logo {
    width: 150px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

nav{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Menu{
    width: 500px;
    height: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Menu li{
    width: 33%;
    height: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    -webkit-clip-path: polygon(20% 0%, 100% 0, 80% 100%, 0% 100%);
    clip-path: polygon(20% 0%, 100% 0, 80% 100%, 0% 100%);
    transition: 200ms all ease-in-out;
}

.Menu li a{
    width: 100%;
    height: 100%;
    color: rgba(255, 255, 255, 0.637);
    display: flex;
    justify-content: center;
    align-items: center;
}

.Menu li:hover{
    background-color: silver;
}

.Menu li:hover a {
    color: black;
}

.Btn-Menu{
    width: 100px;
    height: 100%;
    display: none;
    justify-content: center;
    align-items: center;
}

.Btn-Menu .Btn{
    width: 50px;
    height: 50px;
    border: none;
    background-color: transparent;
    font-size: 30px;
    color: white;
    cursor: pointer;
}

.Login{
    width: 150px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Btn-Login {
    color: rgba(255, 255, 255, 0.637);
    font-weight: 600;
    transition: 200ms all ease-in-out;
}

.Btn-Login:hover{
    color: white;
    border-bottom: 2px solid white;
    transform: rotate(10deg);
}

@media screen and (max-width: 700px){
    .Menu{
        display:none;
    }
    .Btn-Menu{
        display: flex;
    }
}