.containerLogin{
    width: 100%;
    height: 92vh;
    background-color: rgb(0, 0, 0);
    display: flex;
    justify-content: center;
    align-items: center;
}

.AreaLogin{
    width: 100px;
    height: 700px;
    background-color: rgb(0, 0, 0);
    color: white;
    overflow: hidden;
    transition: 1s all ease-in-out;
}

.AreaLogin:hover{
    width: 700px;
}

.AreaLogin:hover h1{ 
    color: blue;
    text-shadow: 0 0 5px blue,
                0 0 25px blue,
                0 0 50px blue,
                0 0 100px blue;
}


.txtLogin{
    position: fixed;
    width: 100px;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.txtLogin h1{
    color: rgb(255, 0, 0);
    text-shadow: 0 0 5px #ff0000,
                0 0 25px #ff0000,
                0 0 50px #ff0000,
                0 0 100px #ff0000;
    transform: rotate(270deg);
    text-transform: uppercase;
    transition: 1s all ease-in-out;
}

.containerAccount{
    width: 100px;
    height: 700px;
    color: white;
    background-color: rgb(0, 0, 0);
    border-left: 1px solid rgba(255, 255, 255, 0.185);
    transition: 1s all ease-in-out;
    overflow: hidden;
}

.containerAccount:hover{
    width: 700px;
}

.containerAccount:hover h1{
    color: blue;
    text-shadow: 0 0 5px blue,
                0 0 25px blue,
                0 0 50px blue,
                0 0 100px blue;
}


.txtAccount{
    position: fixed;
    width: 100px;
    height: 700px;
    display: flex;
    flex-direction: column-reverse;
    justify-content: space-around;
    align-items: center;
    cursor: pointer;
}

.txtAccount h1{
    transform: rotate(270deg);
    text-transform: uppercase;
    color: red;
    text-shadow: 0 0 5px #ff0000,
                0 0 25px #ff0000,
                0 0 50px #ff0000,
                0 0 100px #ff0000;
                transition: 1s all ease-in-out;
}


footer{
    position:fixed;
    bottom: 0;
    width: 100%;
    height: 8vh;
    background-color: black;
    border-top: 1px solid rgba(255, 255, 255, 0.171);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

footer .MediaLogin{
    width: 80%;
}

.MediaLogin ul{
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.MenuLogin ul li a{
    color: rgba(255, 255, 255, 0.616);
    transition: 200ms all ease-in-out;
}

.Autor{
    margin-top: 5px;
    font-size: 15px;
    color: rgba(255, 255, 255, 0.555);
}