#Logon{
    margin-left: 150px;
    width: 80%;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding-top: 50px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.164);
}

#Logon h2{
    color: greenyellow;
    text-shadow: 0 0 5px greenyellow,
                0 0 25px greenyellow,
                0 0 50px greenyellow,
                0 0 100px greenyellow;
}

.user-box {
    width: 80%;
    position: relative;
  }
  
.user-box input {
    width: 100%;
    padding: 10px 0;
    font-size: 16px;
    color: #fff;
    margin-bottom: 30px;
    border: none;
    border-bottom: 1px solid greenyellow;
    outline: none;
    background: transparent;
    text-align: center;
    transition: 500ms all ease-in-out;
  }

  .user-box input:hover {
    padding: 10px 0;
    font-size: 25px;
  }

.user-box label {
    position: absolute;
    top:0;
    left: 0;
    padding: 10px 0;
    font-size: 15px;
    color: #fff;
    pointer-events: none;
    transition: .5s;
  }
  
.user-box input:focus ~ label,
.user-box input:valid ~ label {
    top: -20px;
    left: 0;
    color: greenyellow;
    font-size: 15px;
  }

.button {
    width: 100px;
    height: 50px;
    position: relative;
    display: inline-block;
    display: flex;
    justify-content: center;
    align-items: center;
    color:greenyellow;
    font-size: 16px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    transition: .5s;
    margin-top: 40px;
    letter-spacing: 4px
  }
  
  .button:hover {
    background: greenyellow;
    color: black;
    border-radius: 5px;
    box-shadow: 0 0 5px greenyellow,
                0 0 25px greenyellow,
                0 0 50px greenyellow,
                0 0 100px greenyellow;
  }
  
  .button span {
    position: absolute;
    display: block;
  }
  
  .button span:nth-child(1) {
    top: 0;
    left: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(90deg, transparent, greenyellow);
    animation: btn-anim1 1s linear infinite;
  }
  
  @keyframes btn-anim1 {
    0% {
      left: -100%;
    }
    50%,100% {
      left: 100%;
    }
  }
  
  .button span:nth-child(2) {
    top: -100%;
    right: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(180deg, transparent, greenyellow);
    animation: btn-anim2 1s linear infinite;
    animation-delay: .25s
  }
  
  @keyframes btn-anim2 {
    0% {
      top: -100%;
    }
    50%,100% {
      top: 100%;
    }
  }
  
  .button span:nth-child(3) {
    bottom: 0;
    right: -100%;
    width: 100%;
    height: 2px;
    background: linear-gradient(270deg, transparent, greenyellow);
    animation: btn-anim3 1s linear infinite;
    animation-delay: .5s
  }
  
  @keyframes btn-anim3 {
    0% {
      right: -100%;
    }
    50%,100% {
      right: 100%;
    }
  }
  
  .button span:nth-child(4) {
    bottom: -100%;
    left: 0;
    width: 2px;
    height: 100%;
    background: linear-gradient(360deg, transparent, greenyellow);
    animation: btn-anim4 1s linear infinite;
    animation-delay: .75s
  }
  
  @keyframes btn-anim4 {
    0% {
      bottom: -100%;
    }
    50%,100% {
      bottom: 100%;
    }
  }